import { request } from "./request";

//
export function getMusicUrl(id){
    return request ({
        url: '/song/url',
        params: {
            id
        }
    })
}

//
export function getMusicDetail(ids){
    return request ({
        url: '/song/detail',
        params: {
            ids
        }
    })
}

//
export function getMusicComments(id,limit,offset){
    return request ({
        url: '/comment/music',
        params: {
            id,
            limit,
            offset
        }
    })
}

// 
export function getLyric(id){
    return request ({
        url: '/lyric',
        params: {
            id,
        }
    })
}