<template>
  <div class="LyricList">
    <div @click="hiddrenLyric" class="LyricListContent">
      <ul ref="list" v-if="lyricText.length !== 0">
        <li class="item" v-for="(item, index) in lyricText" :key="index">
          <!-- v-if="item !== '' && item !== '\n'" -->
          <div :style="{color:index === activeIndex?color:'#999'}"
          class="itemSpan">
            {{ item }}
          </div>
        </li>
      </ul>
    </div>
    <div 
     v-show="!sgc"
     class="block">
    <el-color-picker 
     size="mini"
     color-format="rgb"
     v-model="color">
    </el-color-picker>
    </div>
  </div>
</template>

<script>
import { getLyric } from "../../../NetRequest/play";
export default {
  name: "LyricList",
  data() {
    return {
      activeIndex: 0,
      index: 0,
      yscroll: 0,
      songTime: 0, // 
      num: 0,
      lyricText: "",
      songLyric: "",
      sgc:true,
      color:'#eee'
    };
  },
  methods: {
    getLyricData() {
      getLyric(this.$store.state.playingMusicId).then((res) => {
        //   console.log(res);
        this.sgc = res.data.sgc
        if (!this.sgc) {
          let text = res.data.lrc.lyric;
          let reg = /[[|[0-9:.]|]]/gi; //  []
          let lyric = text.replace(reg, "");
          this.lyricText = lyric.split("]"); // 
          var reg2 = /\[(.+?)\]/g; // 
          let time = text.match(reg2); // match 
        
          for (let i = 0; i < time.length; i++) {
            time[i] = time[i].slice(1, 9); // slice 
            time[i] =
              parseFloat(time[i].slice(0, 2), 2) * 60 +
              parseFloat(time[i].slice(3, 8), 2); // 
          }

          this.songLyric = time;
          let num = 0;
          // for
          for (let i = 0; i < this.songLyric.length; i++) {
            //  NaN  0
            if (isNaN(this.songLyric[i])) {
              this.songLyric.splice(i, 1, (num += 0.15));
            }
            if (
              this.songLyric[i] == this.songLyric[i + 1] ||
              this.songLyric[i] >= this.songLyric[i + 1]
            ) {
              this.songLyric.splice(i + 1, 1, this.songLyric[i + 1] + 0.5);
            }
          }
        }else{
            this.lyricText = ['']
        }
      });
    },
    timeUpDate() {
      let scrollHeight = 0;
      let scrollViewHeight =
        document.getElementsByClassName("LyricListContent")[0].clientHeight / 2;

      // 
      if (this.lyricText.length != 1) {
        // 
        if (
          this.$store.state.audio.currentTime >= this.songLyric[this.index] &&
          this.$store.state.audio.currentTime <= this.songLyric[this.index + 1]
        ) {
          let itemToTop = document.getElementsByClassName("itemSpan")[
            this.index
          ].offsetTop;
          let itemClientHeight = document.getElementsByClassName("itemSpan")[
            this.index
          ].clientHeight; // item
          if (itemToTop > scrollViewHeight) {
            scrollHeight = itemToTop - scrollViewHeight + itemClientHeight; // 
            this.$refs.list.style.transform =
              "translateY(" + -scrollHeight + "px)";
          }
          this.index++; // ++
          this.activeIndex++; // ++
        }
      }
    },
    hiddrenLyric() {
      this.$store.state.isShowLyric = false;
    },
    scrollLyric() {
    //   this.$nextTick(() => {
        let scrollHeight = 0;
        this.$bus.$on("playEnd", ({ playEnd }) => {
          if (playEnd) {
            scrollHeight = 0; // 
            this.$refs.list.style.transform =
              "translateY(" + scrollHeight + "px)"; // 
          }
        });
        // 
        this.$store.state.audio.addEventListener("timeupdate", this.timeUpDate);

        //  seeked 
        this.$store.state.audio.addEventListener("seeked", () => {
            if (this.lyricText.length == 1)
             return
          let scrollViewHeight =
            document.getElementsByClassName("LyricListContent")[0]
              .clientHeight / 2;
          let seekedIndex = this.songLyric.filter((item) => {
            // 
            return this.$store.state.audio.currentTime >= item;
          }).length;
          let itemToTop = document.getElementsByClassName("itemSpan")[
            seekedIndex
          ].offsetTop; // item
          let itemClientHeight = document.getElementsByClassName("itemSpan")[
            seekedIndex
          ].clientHeight; // item
          if (itemToTop > scrollViewHeight) {
            // 
            scrollHeight = itemToTop - scrollViewHeight + itemClientHeight; // 
            this.$refs.list.style.transform =
              "translateY(" + -scrollHeight + "px)";
          }
          this.index = seekedIndex; // 
          this.activeIndex = seekedIndex; // 
        });
    //   });
    },
  },
  created() {
    // 
    this.getLyricData();
  },
  mounted() {
    setTimeout(()=>{
        this.scrollLyric();
    },2000)
  },
  computed: {
    // linearSet() {
    //   return this.$store.state.playSong.linearIndex;
    // },
    musicId() {
      return this.$store.state.playingMusicId;
    },
  },
  watch: {
    musicId() {
      // console.log("musicId");
      this.index = 0
      this.activeIndex = 0
      this.getLyricData();
      this.scrollLyric()
    },
  },
  destroyed() {
    this.$store.state.navMusicDom.removeEventListener(
      "timeupdate",
      this.timeUpDate
    );
  },
};
</script>
<style scoped>
.noItem {
  float: left;
  width: 100%;
  height: 0.553928rem;
}
.playbox {
  width: 100%;
}
.list {
  transition: all 0.5s linear;
}
.LyricListContent {
  width: 80%;
  position: relative;
  top: 3%;
  margin: 0 auto;
  height: 90%;
  text-align: center;
  overflow: hidden;
  font-size: 0.346667rem;
  letter-spacing: 0.053333rem;
  color: #757575;
}
.LyricListContent ul {
  transition: 0.3s linear;
}
.active {
  color: #fff;
}
.item {
  margin-top: 17px;
}
.nolyric {
  position: relative;
  font-size: 20px;
  color: #fff;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.block{
    float: right;
}
</style>