import { request } from "./request";

//
export function getBanner(type){
    return request ({
        url: '/banner',
        params: {
            type
        }
    })
}

//
export function getRecommend(limit){
    return request ({
        url: '/personalized',
        params: {
            limit
        }
    })
}

//
export function getNewSongs(type){
    return request({
        url: '/top/song',
        params: {
            type
        }
    })
}

//MV
export function getMV(area,limit){
    return request({
        url: '/mv/first',
        params: {
            area,
            limit
        }
    })
}

//
export function getHotSinger(limit,offset){
    return request({
        url: '/top/artists',
        params: {
            limit,
            offset
        }
    })
}